.generator {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: $c-main;

    &__wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 100vh;
    }

    &__box {
        background-color: $c-fallbakc;
        border-radius: 1.2rem;
        max-width: 52rem;
        width: 100%;
        box-shadow: 3px 3px 8px 0px #84a3ff4a;
        padding: 3.6rem 2.4rem;
        margin: 5rem 0;

        @include breakpoint-down($mobile-width) {
            padding: 2.4rem 1.6rem;
        }
    }

    &__title {
        text-align: center;
        margin-bottom: 3.6rem;
        font-size: 0;

        & > div {
            font-size: 2.8rem;
            display: inline-block;
            padding: 0 0.15rem;
        }
    }

    &__head {
        position: relative;
    }

    &__result {
        font-size: 2.5rem;
        letter-spacing: 0.2rem;
        border-radius: 1.6rem;
        margin-bottom: 2.4rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2.4rem 1.8rem;
        height: 86px;
        background-color: rgba($c-background, 0.5);
        overflow: hidden;
        user-select: none;
        transition: all 0.5s;
        cursor: pointer;
    }

    &__copy-info {
        position: absolute;
        background: #373737;
        padding: 10px 15px;
        display: flex;
        justify-content: center;
        color: #fff;
        top: 0;
        right: 0;
        font-size: 14px;
        border-radius: 4px;
        letter-spacing: 1px;
        opacity: 0;
        pointer-events: none;

        &.appear {
            animation: appear 1s ease;
        }

        @keyframes appear {
            0% {
                opacity: 0;
            }
            20% {
                opacity: 1;
            }
            80% {
                opacity: 1;
            }
            100% {
                transform: translateY(-4.1rem);
                opacity: 0;
            }
        }
    }

    &__settings {
        margin-bottom: 2.4rem;
        &-title {
            font-size: 1.2rem;
            margin-bottom: 1.6rem;
            text-transform: uppercase;
            letter-spacing: 0.2rem;
            text-align: center;
        }

        &-label {
            background-color: rgba($c-background, 0.5);
            padding: 1.8rem 2.4rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-radius: 1.6rem;
            margin-top: 1.6rem;
            cursor: pointer;
            position: relative;
            transition: all 0.5s ease-in-out;

            &::before,
            &::after {
                position: absolute;
                content: "";
                @include center("vertical");
                border-radius: 1.4rem;
                transition: all 0.5s ease-in-out;
            }

            &::before {
                background-color: #505562;
                height: 2.8rem;
                width: 5rem;
                right: 2.4rem;
            }

            &::after {
                height: 2rem;
                width: 2rem;
                right: 5rem;
                background-color: $c-main;
            }
        }

        &-box {
            background-color: rgba($c-background, 0.5);
            padding: 1.8rem 3.6rem;
            border-radius: 1.6rem;
            position: relative;

            &::before,
            &::after {
                position: absolute;
                font-size: 1.6rem;
                @include center("vertical");
                height: 100%;
                width: 3.6rem;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            &::before {
                content: attr(data-min);
                left: 0.3rem;
            }
            &::after {
                content: attr(data-max);
                right: 0;
            }
        }

        &-range {
            width: 100%;
            display: block;
            margin: 0;
            height: 2.4rem;
        }

        &-range[type="range"] {
            -webkit-appearance: none;
            background-color: transparent;
            color: transparent;
            width: 100%;
            &:focus {
                outline: none;
            }
            &::-webkit-slider-runnable-track {
                width: 100%;
                height: 0.4rem;
                cursor: pointer;
                background: #505562;
                border-radius: 0.3rem;
            }
            &::-webkit-slider-thumb {
                height: 1.6rem;
                width: 1.6rem;
                border-radius: 50%;
                background: $c-main;
                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -0.6rem;
            }
            &::-moz-range-track {
                width: 100%;
                height: 0.4rem;
                cursor: pointer;
                background: #505562;
                border-radius: 0.3rem;
            }
            &::-moz-range-thumb {
                height: 1.6rem;
                width: 1.6rem;
                border-radius: 50%;
                background: $c-main;
                cursor: pointer;
            }
            &::-ms-track {
                width: 100%;
                height: 0.4rem;
                cursor: pointer;
                background: transparent;
                border-color: transparent;
                border-width: 16px 0;
                color: transparent;
            }
            &::-ms-thumb {
                height: 1.6rem;
                width: 1.6rem;
                border-radius: 50%;
                background: $c-main;
                cursor: pointer;
            }
        }

        &-length {
            font-size: 1.1rem;
            padding-left: 1.6rem;
            margin-bottom: 0.5rem;
            letter-spacing: 0.1rem;
        }

        &-input {
            height: 0;
            position: absolute;
            width: 0;

            &:checked + .generator__settings-label {
                &::before {
                    background-color: rgb(72, 147, 222);
                    box-shadow: 0 0 0.5rem 0.1rem #fff;
                }

                &::after {
                    right: 2.8rem;
                }
            }

            &:checked:disabled + .generator__settings-label {
                &::before,
                &::after {
                    opacity: 0.65;
                }
            }
        }

        &-text {
            letter-spacing: 0.1rem;
        }
    }

    &__button {
        display: block;
        border: none;
        padding: 0;
        background-color: rgba($c-background, 0.5);
        padding: 1.8rem 2.4rem;
        color: $c-main;
        width: 100%;
        font-family: "Roboto", sans-serif;
        font-weight: 400;
        border-radius: 1.6rem;
        font-size: 1.6rem;
        border: 0.1rem solid #fff;
        transition: all 0.5s ease;
        text-transform: uppercase;
        letter-spacing: 0.3rem;
        outline: none;

        &:hover {
            box-shadow: 0.5rem 0.5rem 0 #fff;
            border-color: transparent;
        }
    }
}
